body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.DateControl__input {
  font-size: 1.1em;
  padding: 0.36em; }

.DateControl {
  display: inline-block;
  margin-right: 40px; }
  .DateControl__label {
    color: grey; }
  .DateControl__input {
    margin: 4px;
    width: 15em; }

.MediaObject:after {
  clear: both;
  display: table;
  content: ' '; }

input {
  font-size: 1.1em;
  padding: 0.36em; }

.MediaObject__icon {
  float: left;
  margin: 1px; }
  img {
    width: 48px;
    height: 48px; }

.MediaObject__content {
  float: left;
  margin: 1px;
  margin-top: 2px; }
  input {
    width: 22em;
    margin-top: 6px !important;
    margin-left: -11px !important;
    border-left: 2px solid black; }

.PagingBtn {
  width: unset;
  min-width: 3em;
  font-size: 1em; }

.App__controls:after {
  clear: both;
  display: table;
  content: ' '; }

.App__header {
  padding: 4px;
  padding-left: 10px;
  background: #1a1a1a;
  color: #9c9c9c;
  font-weight: bold; }

.App__controls {
  margin: 4px; }
  .App__controls-options {
    float: left;
    margin: 20px; }
  .App__controls-auth {
    float: right;
    margin: 20px;
    margin-top: 40px; }
  .App__controls-submit-btn {
    width: 10em;
    font-size: 1em;
    border-left: 2px solid grey; }

.App__error {
  color: red;
  font-style: italic;
  text-align: center; }

.App__total-numbers {
  margin: 4px; }

.App__daily-numbers {
  margin: 4px;
  margin-left: 20px; }

.DataPanel {
  margin: 20px;
  padding: 20px;
  border: 1px solid black;
  display: inline-block; }
  .DataPanel__count {
    font-size: 2em;
    font-weight: bold;
    text-align: center; }
  .DataPanel__name {
    font-weight: bold; }

